<template>
  <div>
    <div class="dengLu" id="dengLu">
      <img class="dengLu-img" src="../../src/assets/001.png" alt="" />
      <br />
      <br />
      <input
        class="input"
        id="UserId"
        type="text"
        placeholder="账号/手机号/邮箱"
      />
      <br />
      <input
        class="input"
        id="pwd"
        type="text"
        placeholder="请输入密码"
        @keyup.enter="dl"
      />

      <button class="dengLoing" @click="dl">登陆</button>
      <p class="zhuCe">立即注册</p>
      <p class="zhaoHui">找回密码</p>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    dl: function() {
      if (UserId.value == "") {
        alert("手机号不允许为空");
      } else if (UserId.length < 11) {
        alert("手机号码不能少于11位");
      } else if (pwd.value == "") {
        alert("密码不允许为空");
      } else {
        alert("登陆成功!");
        localStorage.setItem("lastname", UserId.value);
        location.href = "Index";
        // location.href = "index?" + "Userid=" + encodeURI(UserId.value);
      }
    }
  }
};
</script>
<style>
.input {
  width: 99%;
  height: 0.4rem;
  text-indent: 0.2rem;
  border: 1px white solid;
  border-bottom: 1px dashed black;
  font-size: 0.13rem;
  margin-top: 1.2em;
}
.dengLu {
  height: 5rem;
  margin-top: 4.5em;
}
.dengLoing {
  width: 90%;
  height: 0.4rem;
  margin-top: 1.5em;
  background-color: red;
  color: white;
  border: 1px white solid;
}
.zhuCe {
  float: left;
  font-size: 0.9em;
  margin-top: 0.9em;
  margin-left: 0.5em;
}
.zhaoHui {
  float: right;
  font-size: 0.9em;
  margin-top: 0.9em;
  margin-right: 0.5em;
}
.dengLu-img {
  width: 30%;
  border: 1px red solid;
  border-radius: 50%;
  height: 8em;
}
</style>
